@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat-Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: local("Montserrat-ExtraBold"), url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a:focus,
a:active {
  outline: none; }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  line-height: 1; }

input,
button,
textarea {
  font-family: "Montserrat", "Arial", sans-serif; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit; }

.container {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 320px;
  margin: 0 auto; }
  @media (min-width: 320px) {
    .container {
      padding: 58px 0 61px; } }
  @media (min-width: 1280px) {
    .container {
      padding: 75px 0;
      max-width: 1120px; } }

.main {
  padding: 40px 0;
  font-family: "Montserrat", "Arial", sans-serif;
  background: #e5e5e5; }

.order__wrap {
  box-shadow: 0 24px 80px rgba(0, 0, 0, 0.1); }
  @media (min-width: 1280px) {
    .order__wrap {
      display: grid;
      grid-template-columns: 1fr 1fr; } }

@media (min-width: 1280px) {
  .order__left-side {
    position: relative;
    overflow: hidden; }
    .order__left-side::after {
      pointer-events: none;
      content: "";
      position: absolute;
      width: 688px;
      height: 688px;
      background: transparent;
      top: -97px;
      left: 235px;
      border-radius: 50%;
      border: 20px solid rgba(255, 255, 255, 0.03); } }

.left-side {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #ffffff;
  padding: 42px 20px 37px;
  color: #212121; }
  @media (min-width: 1280px) {
    .left-side {
      grid-template-rows: 1fr 1fr;
      background: #428a60 linear-gradient(180deg, #428a60 0%, #35734f 100%);
      color: #ffffff;
      padding: 40px;
      position: relative; } }

.left-side__item-name {
  display: inline-block;
  font-weight: bold;
  font-size: 18px;
  line-height: 130%;
  position: relative;
  z-index: 5; }

.left-side__item-type {
  display: inline-block;
  font-weight: bold;
  font-size: 18px;
  line-height: 130%;
  text-transform: uppercase;
  justify-self: end;
  position: relative;
  z-index: 5; }
  @media (min-width: 1280px) {
    .left-side__item-type {
      grid-row: 2/3;
      align-self: flex-end;
      justify-self: start;
      line-height: 18px; } }

.left-side__item-img-wrap {
  grid-column: 1/-1;
  width: 223px;
  height: 122px;
  position: relative;
  margin: 56px 0 32px 39px; }
  .left-side__item-img-wrap::after {
    content: "";
    position: absolute;
    top: -48px;
    left: 0;
    width: 202px;
    height: 202px;
    border-radius: 50%;
    background: #428a60;
    z-index: 1; }
  @media (min-width: 1280px) {
    .left-side__item-img-wrap {
      position: absolute;
      bottom: 33px;
      left: 17px;
      width: 465px;
      height: 255px;
      margin: 0; }
      .left-side__item-img-wrap::after {
        width: 398px;
        height: 398px;
        background: #428a60;
        top: -141px;
        left: -37px; } }

.left-side__item-img {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.right-side {
  background: #ffffff; }

.right-side__title {
  text-align: center;
  font-weight: 800;
  font-size: 30px;
  line-height: 130%;
  max-width: 320px;
  margin: 0 auto; }
  .right-side__title br {
    display: none; }
  @media (min-width: 1280px) {
    .right-side__title {
      background: #f8f8f8;
      margin: 0;
      max-width: 100%;
      padding: 29px; }
      .right-side__title br {
        display: block; } }

.right-side__form {
  max-width: 320px;
  margin: 0 auto; }

.right-side__footer {
  height: 40px;
  background: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (min-width: 1280px) {
    .right-side__footer {
      display: none; } }

.right-side__footer-text {
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: baseline; }

.right-side__footer-icon {
  fill: none;
  margin: 0 20px; }

.form {
  display: grid;
  padding: 28px 20px;
  grid-template-columns: 174px 1fr;
  -moz-column-gap: 10px;
       column-gap: 10px; }
  @media (min-width: 1280px) {
    .form {
      padding: 52px 0 60px;
      grid-template-columns: 184px 1fr;
      -moz-column-gap: 20px;
           column-gap: 20px; } }

.form__label {
  font-weight: 800;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-bottom: 10px; }
  .form__label span {
    color: #428a60; }

.form__input {
  background: #f8f8f8;
  border: 1px solid #e4e4e4;
  box-sizing: border-box;
  border-radius: 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  height: 50px;
  padding: 0 10px;
  text-transform: capitalize;
  max-width: 100%; }
  .form__input--name {
    grid-column: 1/-1;
    margin-bottom: 20px; }
  .form__input--tel {
    grid-column: 1/2;
    margin-bottom: 28px; }
    @media (min-width: 1280px) {
      .form__input--tel {
        margin-bottom: 38px; } }
  .form__input::-moz-placeholder {
    color: rgba(33, 33, 33, 0.2); }
  .form__input:-ms-input-placeholder {
    color: rgba(33, 33, 33, 0.2); }
  .form__input::placeholder {
    color: rgba(33, 33, 33, 0.2); }
  @media (min-width: 1280px) {
    .form__input {
      padding: 0 20px; } }

.form__btn-submit {
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 42px;
  border-radius: 5px;
  background: #428a60;
  color: #ffffff;
  fill: #ffffff;
  grid-column: 1/-1; }
  .form__btn-submit span {
    margin-right: 10px; }

.lang-select {
  display: grid;
  grid-template-columns: repeat(3, -webkit-min-content);
  grid-template-columns: repeat(3, min-content);
  gap: 10px;
  padding: 0 10px;
  align-items: center;
  justify-content: center; }
  @media (min-width: 1280px) {
    .lang-select {
      padding: 0 20px; } }

.lang-select__text {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: #212121; }

.lang-select__icon-dropdown {
  fill: none; }
